@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* Import */


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body{
  font-family: 'Poppins', sans-serif !important;
  color: #fff !important;
  overflow-x: hidden;
}

.bg-color-1{
  background-color: #191919;
}

.bg-color-2{
  background-color: #101010;
}
header{width:100%;height:100vh;background:linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)),url(/portfolio/static/media/bg-hero.9575c5a2.jpg) center no-repeat;background-size:cover;transition:ease-in-out 2s}header nav{padding:2rem 0 0 0;-webkit-justify-content:space-between;justify-content:space-between}header nav h1{color:#fff;padding-left:3rem;font-weight:normal;font-size:1.4rem}header nav ul li{list-style:none}header nav ul li a{text-decoration:none;color:#fff;padding:1rem 0;margin:0 1rem;position:relative;letter-spacing:2px;font-size:1.2rem;font-weight:500}header nav ul li a:hover{color:#1ea7c2;text-decoration:none}header nav ul li a.active{color:#1ea7c2 !important}header .scroll{position:fixed;top:0;background-color:#191919;width:100%;z-index:99}header .scroll nav{padding:0 50px}header .container{height:100%}header .container h1{font-weight:bold}header span{color:rgba(198,201,216,.75)}header span.colored-title{color:#1ea7c2}header .mmenu{font-size:1.5rem;height:100vh;background-color:#101010;position:fixed;top:0;right:0;z-index:9999999}header .mmenu li{padding:32px 50px}header .mmenu .close-btn{position:absolute;right:0;margin:21px 10px;cursor:pointer}header .linkedin-desktop{padding:14px 0;font-size:1.2rem}@media screen and (min-width: 320px){header .desktop-nav,header .linkedin-desktop{display:none !important}header .title h1{font-size:30px}}@media screen and (min-width: 768px){header .title h1{font-size:53px}}@media screen and (min-width: 992px){header .desktop-menu{padding:0 60px}header .desktop-nav{display:-webkit-flex !important;display:flex !important}header .linkedin-desktop{display:block !important}header .title h1{font-size:72px}}
#about{height:100%}#about img{width:100%;border-radius:10px}#about h2{font-size:60px}#about .description{color:rgba(198,201,216,.75);font-size:18px;line-height:2em;font-weight:300}#about .nav-tabs{border:none}#about .nav-tabs .nav-link{background:none !important;border-bottom:1px solid #fff;margin:0 10px;color:#fff !important;padding:0;margin:20px 30px 25px 0;font-size:18px}#about .nav-tabs .nav-link:hover{border:none !important;border-bottom:1px solid #fff !important}#about .nav-tabs .nav-link.active{border:none;border-bottom:1px solid #1ea7c2 !important;color:#1ea7c2 !important}#about .tab-text{color:rgba(198,201,216,.75);font-weight:300}#about .tab-text span{font-weight:100}
#portfolio{min-height:600px;padding:120px 0}#portfolio h2{font-size:60px}#portfolio p{font-size:18px;color:rgba(198,201,216,.75)}#portfolio .row{width:100%}#portfolio .portfolio-bg{background-size:cover !important;width:100%;min-height:600px;border-radius:10px}#portfolio .portfolio-bg:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}#portfolio .content{position:absolute;bottom:15%;left:40px;right:40px}#portfolio .content p{color:rgba(198,201,216,.75);font-size:14px}#portfolio .content .portfolio-btn{text-decoration:none;color:#fff;border:1px solid #fff;border-radius:5px;padding:7px;margin:7px;transition:all .2s}#portfolio .content .portfolio-btn:hover{background-color:#101010;border:none}#portfolio .content .portfolio-btn_second{text-decoration:none;color:#fff;background-color:#101010;border-radius:5px;border:none;padding:7px;margin:7px;transition:all .2s}#portfolio .content .portfolio-btn_second:hover{background-color:transparent;border:1px solid #fff}#portfolio .github-portfolio-btn{text-decoration:none;color:#fff;background-color:#1ea7c2;font-size:20px;border-radius:5px;border:none;padding:20px;margin:20px;transition:all .2s}#portfolio .github-portfolio-btn:hover{background-color:transparent;border:1px solid #fff}@media screen and (min-width: 991px)and (max-width: 1200px){#portfolio .portfolio-btn{padding:5px !important;margin:5px !important}#portfolio .portfolio-btn_second{padding:5px !important;margin:5px !important}}
#coding{min-height:600px;padding:120px 0}#coding h2{font-size:60px}#coding p{font-size:18px;color:rgba(198,201,216,.75)}#coding .row{width:100%}#coding .portfolio-bg{background-size:cover !important;width:100%;min-height:600px;border-radius:10px}#coding .portfolio-bg:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}#coding .content{position:absolute;bottom:15%;left:40px;right:40px}#coding .content p{color:rgba(198,201,216,.75);font-size:14px}#coding .content .portfolio-btn{text-decoration:none;color:#fff;border:1px solid #fff;border-radius:5px;padding:10px;margin:10px;transition:all .2s}#coding .content .portfolio-btn:hover{background-color:#1ea7c2;border:none}#coding .content .portfolio-btn_second{text-decoration:none;color:#fff;background-color:#1ea7c2;border-radius:5px;border:none;padding:10px;margin:10px;transition:all .2s}#coding .content .portfolio-btn_second:hover{background-color:transparent;border:1px solid #fff}#coding .github-portfolio-btn{text-decoration:none;color:#fff;background-color:#1ea7c2;font-size:20px;border-radius:5px;border:none;padding:20px;margin:20px;transition:all .2s}#coding .github-portfolio-btn:hover{background-color:transparent;border:1px solid #fff}
#contact .container{padding:100px 0}#contact h2{font-size:60px}#contact p{color:rgba(198,201,216,.75);font-size:23px}#contact a{color:#fff;text-decoration:none}#contact a:hover{text-decoration:none}#contact img{width:100%;border-radius:5px}#contact input{width:300px;height:50px;border-radius:5px;border:1px solid #fff;padding:20px;margin:10px 0;color:#fff;background:transparent}#contact input:focus{outline:none}#contact textarea{width:300px;height:100%;border-radius:5px;border:1px solid #fff;padding:20px;margin:10px 0;background:transparent;color:#fff}#contact textarea:focus{outline:none}#contact button{text-decoration:none;color:#fff;background-color:#1ea7c2;border-radius:5px;width:100px;border:none;padding:10px;margin:10px;transition:all .2s}#contact button:hover{background-color:transparent;border:1px solid #fff}
#footer{padding:50px 0}#footer .col-sm-12{margin:15px 0}@media screen and (min-width: 320px)and (max-width: 768px){#footer{text-align:center}}@media screen and (min-width: 1200px){#footer{padding:30px 0px 0 350px}}#footer .social-linkedin{color:#fff;text-decoration:none}#footer .social-linkedin:hover{color:#1ea7c2}#footer .copyright{font-size:14px;color:rgba(198,201,216,.75)}
